<script>
  import { cardSets } from "./Sets.svelte";
  import { beforeUpdate, afterUpdate } from "svelte";

  export let card;
  let set;
  let code;
  let count;
  let index;
  let src;
  let name;
  let cost;
  let region;

  beforeUpdate(() => {
    set = card.set;
    code = card.code;
    count = card.count;

    index = cardSets[set].findIndex(obj => obj.cardCode == code);
    src = cardSets[set][index].assets[0].fullAbsolutePath;
    src = 'img/cards/'+code+'-full.png';
    name = cardSets[set][index].name;
    cost = cardSets[set][index].cost;
    region = cardSets[set][index].regionRef;
  });
</script>

